.auth-popup {
    overflow-y: hidden;
    overflow-x: hidden;
    width: 500px;
    background-color: #f8f8f8;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.auth-popup h2 {
    font-size: 24px;
    margin-bottom: 15px;
}

.auth-popup input {
    width: 80%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.auth-popup button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.auth-popup button:hover {
    background-color: #0056b3;
}
