.calendar{
    width: 98%;
    margin-left: 1%;
    border: 2px solid #ccc;
    border-radius: 5px;
}

body, table {
    margin: 0;
    padding: 0;
    border-collapse: collapse;
}

/* General typography styles */
body {
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 16px;
    line-height: 1.5;
}

/* Table styles */
.apple-table {
    min-height: 300px;
    width: 100%;
    border: 1px solid #d8d8d8;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.apple-table td {
    max-height: 20px;
    padding: 10px;
    border-bottom: 1px solid #d8d8d8;
}

/* Header style */
.apple-table td:first-child {
    font-weight: bold;
    color: #333;
    width: 150px;
}


/* Alternating row colors */
.apple-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

/* Hover effect */
.apple-table tbody tr:hover {
    background-color: #e5e5e5;
}

.apple-button {
    background-color: #0070c9;
    color: #fff;
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
}

.apple-button:hover {
    background-color: #005ba1;
}

/* Center the button */
.centered-cell {
    text-align: center;
    vertical-align: middle;
}