.external-drop-calendar {
    border-right: 1px solid #ccc;
}

.external-drop-task {
    color: #fff;
    padding: 10px;
    margin: 20px;
    border-radius: 8px;
    font-family: -apple-system, Segoe UI, Roboto, sans-serif;
}

.external-drop-task-blank {
    color: #999;
    border-width: 2px;
    border-style: dotted;
}


button {
    padding: 8px 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.scheduler_box{
    margin-top: 20px;
    margin-left: 5%;
    width: 90%;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.event-color-c {
    display: flex;
    margin: 16px;
    align-items: center;
    cursor: pointer;
}

.event-color-label {
    flex: 1 0 auto;
}

.event-color {
    width: 30px;
    height: 30px;
    border-radius: 15px;
    margin-right: 10px;
    margin-left: 240px;
}

.crud-color-row {
    display: flex;
    justify-content: center;
    margin: 5px;
}

.crud-color-c {
    padding: 3px;
    margin: 2px;
}

.crud-color {
    position: relative;
    min-width: 46px;
    min-height: 46px;
    margin: 2px;
    cursor: pointer;
    border-radius: 23px;
}

.crud-color-c.selected,
.crud-color-c:hover {
    box-shadow: inset 0 0 0 3px #007bff;
    border-radius: 48px;
}

.crud-color:before {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    margin-left: -10px;
    color: #f7f7f7;
    font-size: 20px;
    text-shadow: 0 0 3px #000;
    display: none;
}

.crud-color-c.selected .crud-color:before {
    display: block;
}
.mbsc-timeline-parent { height: 132px; }

.upload-button-container {
    text-align: center;
    margin-top: 20px;
}

.upload-button {
    font-size: 18px;
    padding: 15px 25px;
    border-radius: 10px;
    cursor: pointer;
}

.upload-button svg {
    margin-right: 8px;
}

.upload-button {
    background-color: #007AFF; /* Replace with the desired Apple color */
    color: white;
}

.upload-button:hover {
    background-color: #0052CC; /* Replace with the desired Apple hover color */
}
/* Add this CSS to the existing CSS file or create a new one */
.picture-window {
    margin-top: 20px;
    text-align: center;
}

.picture-window img {
    height: 250px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

@media (max-width: 767px) {
    /* This media query targets screens with a maximum width of 767px, typically phones */
    .picture-window img {
        height: 150px; /* Adjusted height for phones */
    }
}

